import { Component, OnInit } from '@angular/core';
import { DeviceService } from 'src/app/core/services/device.service';
import { Title } from '@angular/platform-browser';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';
import { Router } from '@angular/router';
import { SeoService } from 'src/app/core/services/header/seo.service';

@Component({
  selector: 'app-mes-do-consumidor',
  templateUrl: './mes-do-consumidor.component.html',
  styleUrls: ['./mes-do-consumidor.component.scss']
})
export class MesDoConsumidorComponent implements OnInit {

  public steps: IBreadcrumb[] = [
    {
      id: '0',
      label: 'Voltar para home',
      link: '/'
    },
    {
      id: '1',
      label: 'Promoções',
      link: '/promocoes'
    },
    {
      id: '2',
      label: 'Regulamento Mês do Consumidor Zero KM'
    }
  ];

  public isMobile: boolean;
  public screenTypeBySize: string;


  constructor(
    private titleS: Title,
    private device: DeviceService,
    private router: Router,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.titleS.setTitle('Zero Km - Regulamento Mês do Consumidor Zero KM');
    this.seoService.createLinkForCanonicalURL();
    this.isMobile = this.device.isMobile();
    this.screenTypeBySize = this.device.screenTypeBySize();
  }


  goToLojas() {
    this.router.navigate(['lojas'])
  }
}
